import React, { Component } from "react";
import Scoreboard from "./Scoreboard";
import Images from "./Images";

const originalArray = [
    {
        location: 0,
        clicked: false,
        url: "https://static.giantbomb.com/uploads/square_small/0/1122/187741-avatar_the_legend_of_aang_ps2.jpg"
    },
    {
        location: 1,
        clicked: false,
        url: "https://static.giantbomb.com/uploads/square_small/0/7383/1810812-angelicapro.jpg",
    },
    {
        location: 2,
        clicked: false,
        url: "https://static.giantbomb.com/uploads/square_small/0/5768/764402-bootsthemonkey.jpg"
        },
    {
        location: 3,
        clicked: false,
        url: "https://static.giantbomb.com/uploads/square_small/0/3698/375861-chuckie4.jpg"
        },
    {
        location: 4,
        clicked: false,
        url: "https://static.giantbomb.com/uploads/square_small/0/1697/267605-jimmy_13_1_.jpg"
    },
    {
        location: 5,
        clicked: false,
        url: "https://static.giantbomb.com/uploads/square_small/0/966/461214-squidbio.jpg"
    },
    {
        location: 6,
        clicked: false,
        url: "https://static.giantbomb.com/uploads/square_small/0/9493/1743979-images.jpg"
    },
    {
        location: 7,
        clicked: false,
        url: "https://static.giantbomb.com/uploads/square_small/0/3083/1074517-otto_1.png"
    }
];

class Game extends Component {

    state = {
        score: 0,
        topScore: 0,
        imageArray: []
    };

    componentDidMount() {
        var copy = originalArray;

        this.setState({
            imageArray: copy
        });
    };

    clickHandler = (event, data) => {
        event.preventDefault();

        let tempArray = this.state.imageArray;
        let currentScore = this.state.score;

        if (data.clicked === true) {
            if(this.state.topScore > currentScore) {
                this.setState({
                    score: 0,
                });
                console.log('topscore greater than current score');
            } else {
                this.setState({
                    topScore: currentScore,
                    score: 0,
                });
                console.log('new topscore');
            };
            this.setState({
                imageArray: originalArray
            });
            this.resetArray(originalArray);
            this.shuffle(originalArray);
            this.setState({
                imageArray: originalArray
            });
        } else {
            tempArray[data.location].clicked = true;
            this.shuffle(tempArray);
            this.setState({
                score: this.state.score + 1,
                imageArray: tempArray,
            });
            console.log(data);
        };
    };

    resetArray = arr => {
        for (let i = 0; i < arr.length; i++) {
            arr[i].clicked = false;
        };
        return arr;
    };

    shuffle = arr => {
        var currentIndex = arr.length, temporaryValue, randomIndex;
      
        // While there remain elements to shuffle...
        while (0 !== currentIndex) {
      
          // Pick a remaining element...
          randomIndex = Math.floor(Math.random() * currentIndex);
          currentIndex -= 1;
      
          // And swap it with the current element.
          temporaryValue = arr[currentIndex];
          arr[currentIndex] = arr[randomIndex];
          arr[randomIndex] = temporaryValue;
        };

        for (let i = 0; i < arr.length; i++) {
            arr[i].location = i;
        };
      
        console.log(arr);
        return arr;
    }

    returnImages = () => {
        return(
            <div>
                {this.state.imageArray.map(images => {
                    return(<Images key={images.location} source={images.url} confirm={(event) => {this.clickHandler(event, images)}} />)
                })}
            </div>
        );
    };


    render() {
        return(
            <div>
                <Scoreboard score={this.state.score} topscore={this.state.topScore}/>
                <div className="text-center">
                    {this.returnImages()}
                </div>
            </div>
        )
    };
}

export default Game;